@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none; }

.logo {
  position: absolute;
  width: 65px;
  height: 65px;
  top: 10px;
  left: 5%; }
  @media (max-width: 823px) {
    .logo {
      width: 40px;
      height: 40px; } }
  @media (max-width: 414px) {
    .logo {
      width: 60px;
      height: 60px; } }

.logo img {
  width: 100%; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 5;
  background: url(../image/bg/bg_main.png);
  opacity: .85; }
  .header input {
    position: absolute;
    opacity: 0; }

.navbar {
  height: 95px;
  padding-top: 5px; }
  .navbar ul {
    text-align: right;
    padding-right: 8%; }
    .navbar ul li {
      position: relative;
      display: inline-block;
      padding: 20px 20px; }
      .navbar ul li a {
        font-size: 18px;
        font-family: Microsoft JhengHei;
        font-weight: bold;
        text-decoration: none;
        color: #8a8a8a; }
  @media (min-width: 1367px) {
    .navbar {
      height: 110px; }
      .navbar ul {
        text-align: right;
        padding-right: 8%; }
        .navbar ul li a {
          font-size: 28px; } }
  @media (max-width: 823px) {
    .navbar {
      height: 65px; }
      .navbar ul {
        text-align: right;
        padding-right: 4%; }
        .navbar ul li {
          padding: 15px 20px; }
          .navbar ul li a {
            font-size: 16px; } }

.navbar li a:after {
  position: absolute;
  left: 25%;
  bottom: 0;
  margin: auto;
  content: attr(data-hovertext);
  font-size: 14px;
  font-style: italic;
  font-weight: bold;
  font-family: Arial;
  color: rgba(255, 255, 255, 0);
  transform: translateY(-40%) translateX(0%);
  transition: .5s; }
  @media (min-width: 1367px) {
    .navbar li a:after {
      font-size: 20px; } }

.navbar li:nth-child(1):hover a {
  color: #563529;
  transition: .5s; }
  @media (max-width: 414px) {
    .navbar li:nth-child(1):hover a {
      color: #eee; } }

.navbar li:nth-child(2):hover a {
  color: #563529;
  transition: .5s; }
  @media (max-width: 414px) {
    .navbar li:nth-child(2):hover a {
      color: #eee; } }

.navbar li:nth-child(3):hover a {
  color: #563529;
  transition: .5s; }
  @media (max-width: 414px) {
    .navbar li:nth-child(3):hover a {
      color: #eee; } }

.navbar li:nth-child(4):hover a {
  color: #563529;
  transition: .5s; }
  @media (max-width: 414px) {
    .navbar li:nth-child(4):hover a {
      color: #eee; } }

.navbar li:nth-child(1) a:after {
  transform: translateY(-40%) translateX(15%); }

.navbar li:nth-child(3) a:after {
  transform: translateY(-40%) translateX(-12%); }

.navbar li:nth-child(4) a:after {
  transform: translateY(-40%) translateX(-6%); }

.navbar li:nth-child(1):hover a:after {
  color: #563529;
  transform: translateY(10%) translateX(15%); }

.navbar li:nth-child(2):hover a:after {
  color: #563529;
  transform: translateY(10%) translateX(0%); }

.navbar li:nth-child(3):hover a:after {
  color: #563529;
  transform: translateY(10%) translateX(-12%); }

.navbar li:nth-child(4):hover a:after {
  color: #563529;
  transform: translateY(10%) translateX(-6%); }

.navbar li a:before {
  position: absolute;
  bottom: -5px;
  left: 23px;
  display: inline-block;
  content: '';
  width: 0%;
  height: 2px;
  background-color: #563529;
  transition: .5s; }

.navbar li:hover a:before {
  width: 60%; }

.share {
  display: none; }

@media screen and (max-width: 500px) {
  .header {
    opacity: 1;
    background-color: #eee; }
  .header .logo img {
    width: 80%;
    border-radius: 0%; }
  .navbar .menu {
    width: 100vw;
    height: calc(100vh - 80px);
    margin: 0;
    padding-top: 50px;
    background-color: #572600;
    position: fixed;
    top: 80px;
    left: 0;
    border-radius: 50%;
    transform: perspective(900px) rotateX(-450deg);
    transition: .5s;
    opacity: 0; }
  .navbar {
    height: 80px; }
    .navbar ul {
      text-align: center;
      padding-right: 0%; }
      .navbar ul li {
        display: block;
        width: 35%;
        margin: auto;
        margin-top: 15px;
        padding: 10px; }
        .navbar ul li a {
          font-size: 20px;
          padding: 2vmin 4vmin;
          color: #eee; }
      .navbar ul li:first-child {
        margin-top: 15vmin; }
      .navbar ul li:after {
        content: '';
        position: absolute;
        left: -30%;
        bottom: 0;
        width: 160%;
        height: 1px;
        border-bottom: 1px dashed #dd9; } }
  @media screen and (max-width: 500px) and (max-width: 414px) {
    .navbar ul {
      text-align: center;
      padding-right: 0%; }
      .navbar ul li {
        display: block;
        width: 35%;
        margin: auto;
        margin-top: 15px;
        padding: 10px; }
        .navbar ul li a {
          font-size: 20px;
          padding: 2vmin 4vmin;
          color: #eee; }
      .navbar ul li:first-child {
        margin-top: 15vmin; }
      .navbar ul li:after {
        content: '';
        position: absolute;
        left: -30%;
        bottom: 0;
        width: 160%;
        height: 1px;
        border-bottom: 1px dashed #dd9; } }
  @media screen and (max-width: 500px) and (max-width: 360px) {
    .navbar ul li {
      width: 40%; } }
  @media screen and (max-width: 500px) and (max-width: 415px) {
    .navbar ul li a {
      font-size: 18px;
      letter-spacing: 1.2px; } }
  @media screen and (max-width: 500px) and (max-width: 320px) {
    .navbar ul li {
      margin-top: 10px;
      padding: 5px; }
      .navbar ul li a {
        font-size: 16px;
        letter-spacing: 1.1px;
        padding: 5px 10px;
        color: #eee; } }

@media screen and (max-width: 500px) {
  .navbar li a:after,
  .navbar li a:before {
    display: none; }
  nav label {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 0;
    bottom: 0;
    right: 30px;
    margin: auto;
    font-size: 0; }
  nav label:before {
    content: '';
    width: 26px;
    height: 3px;
    background-color: #6f4436;
    transition: .5s;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    box-shadow: 0px 8px 0px #6f4436, 0px -8px 0px #6f4436; }
  nav label:after {
    content: '';
    width: 26px;
    height: 3px;
    background-color: #6f4436;
    transition: .5s;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    opacity: 0; }
  input:checked ~ .menu {
    /*top: 80px;*/
    /*transform: perspective(800px) rotateX(0deg);
		opacity: 1;*/ }
  input:checked ~ label:before {
    transition: .5s;
    transform: rotateZ(45deg);
    box-shadow: 0px 8px 0px transparent, 0px -8px 0px transparent; }
  input:checked ~ label:after {
    transition: .5s;
    transform: rotateZ(-45deg);
    opacity: 1; }
  input:checked ~ .black {
    content: '';
    position: fixed;
    height: calc(100vh - 80px);
    width: 100%;
    top: 80px;
    left: 0;
    transition: .5s;
    background-color: rgba(0, 0, 0, 0.6); }
  /*改用v-model控制*/
  .navbar .menu.move {
    transform: perspective(800px) rotateX(0deg);
    opacity: 1; }
  .share {
    position: relative;
    display: block;
    margin-top: 30vmin;
    text-align: center; }
  .share:before {
    content: 'share';
    position: absolute;
    top: -40px;
    left: 0;
    right: 0;
    font-size: 20px;
    color: #dd9; }
  .share .icon {
    display: inline-block;
    width: 35px;
    height: 35px;
    margin: 0 10px; }
  .share .icon img {
    width: 100%; } }
